import { useForm } from "../../hooks/use-form";
import { useDispatch } from "react-redux";
import { useNavigate, Link, useLocation } from "react-router-dom";
import cn from "classnames";
import { signupUser } from "../../services/actions/auth";
import {
  Input,
  Button,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "./loginPage.module.scss";
import { AppDispatch } from "../../services/store";

export const SignUpPage = () => {
  const dispatch = useDispatch<AppDispatch>();
  const navigate = useNavigate();
  const location = useLocation();
  const controlInput = useForm();

  const handleSignUp = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(signupUser({ ...controlInput?.values })).then(() => {
      navigate(location?.state?.from || "/");
    });
  };

  return (
    <div className={styles.content}>
      <div className="text text_type_main-medium"></div>
      <form onSubmit={handleSignUp} className={styles.form}>
        <Input
          onChange={controlInput.handleChange}
          value={controlInput?.values?.name || ""}
          name={"name"}
          placeholder=""
          contentEditable={true}
          extraClass="mb-2"
        />
        <Input
          onChange={controlInput.handleChange}
          value={controlInput?.values?.email || ""}
          name={"email"}
          placeholder="Email"
          contentEditable={true}
          extraClass="mb-2"
        />
        <PasswordInput
          onChange={controlInput.handleChange}
          value={controlInput?.values?.password || ""}
          name={"password"}
          placeholder=""
          extraClass="mb-2"
        />
        <Button
          htmlType="submit"
          type="primary"
          size="medium"
          extraClass="ml-2"
        >
          {` `}
        </Button>
      </form>
      <div className={cn(styles.questions, "mt-15")}>
        <div>
           ?
          <Link className={styles.link} to="/login">
            {` `}
          </Link>
        </div>
      </div>
    </div>
  );
};
