import { useForm } from "../../hooks/use-form";
import cn from "classnames";
import { Link, useNavigate, useLocation } from "react-router-dom";
import {
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "./loginPage.module.scss";
import { resetPassword } from "../../utils/api";

export const ForgotPasswordPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const controlInput = useForm();

  const handleResetPassword = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    controlInput?.values.email &&
      resetPassword(controlInput.values.email).then(
        (response) =>
          response.success === true &&
          navigate("/reset-password", { state: { from: location.pathname } })
      );
  };

  return (
    <div className={styles.content}>
      <div className="text text_type_main-medium"> </div>
      <form onSubmit={handleResetPassword} className={styles.form}>
        <Input
          onChange={controlInput.handleChange}
          value={controlInput?.values?.email || ""}
          name={"email"}
          placeholder=" email"
          extraClass="mb-2"
        />
        <Button
          htmlType="submit"
          type="primary"
          size="medium"
          extraClass="ml-2"
        >
          cc
        </Button>
      </form>
      <div className={cn(styles.questions, "mt-15")}>
        <div>
           ?{" "}
          <Link className={styles.link} to="/login">
            
          </Link>
        </div>
      </div>
    </div>
  );
};
