import { useDispatch } from "react-redux";
import { useForm } from "../../hooks/use-form";
import cn from "classnames";
import { Link, useLocation, Navigate } from "react-router-dom";
import {
  Input,
  Button,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "./loginPage.module.scss";
import { AppDispatch } from "../../services/store";
import { updatePassword } from "../../services/actions/auth";

export const ResetPasswordPage = () => {
  const dispatch = useDispatch<AppDispatch>();
  const controlInput = useForm();
  const location = useLocation();

  const handleUpdatePassword = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    controlInput?.values &&
      dispatch(updatePassword({ ...controlInput?.values }));
  };

  return location.state?.from !== "/forgot-password" ? (
    <Navigate to={"-1"} />
  ) : (
    <div className={styles.content}>
      <div className="text text_type_main-medium"> </div>
      <form onSubmit={handleUpdatePassword} className={styles.form}>
        <PasswordInput
          onChange={controlInput.handleChange}
          value={controlInput?.values?.password || ""}
          name={"password"}
          placeholder="  "
          extraClass="mb-2"
        />
        <Input
          onChange={controlInput.handleChange}
          value={controlInput?.values?.token || ""}
          name={"token"}
          placeholder="   "
          extraClass="mb-2"
        />
        <Button
          htmlType="submit"
          type="primary"
          size="medium"
          extraClass="ml-2"
        >
          
        </Button>
      </form>
      <div className={cn(styles.questions, "mt-15")}>
        <div>
           ?
          <Link className={styles.link} to="/login">
            {` `}
          </Link>
        </div>
      </div>
    </div>
  );
};
